var times = {};

function format() {
    //begin time && end time
    // name=>username parent
    //
    var begin, end;
    var origin = data.unshift(1);
    begin = end = Date.parse(origin.event_time);
    origin.parentid = "null";
    origin.id = 1;
    for (var i = 2; i <= datas.length; i++) {
        data = datas[i];
        data.id = i;
        data.parentid = parseInt(8 * Math.random())
        data.event_time = Date.parse(data.event_time);
        if (data.event_time < begin) {
            begin = data.event_time;
        }

        if (data.event_time > end) {
            end = data.event_time;
        }

        if (times[data.event_time]) {
            times.push(data);
        } else {
            times[data.event_time] = [data];
        }
    }
}

function travel(begin = 0, end) {
    var fullfiled = Object.keys(times).filter(x => x.event_time < end && x.event_time > begin);
    var flatten = [];
    fullfiled.forEach(function(time) {
        var datas = times[time];
        flatten.concat(datas);
    })
    unflatten(flatten);
}

function unflatten(array, parent, tree) {
    tree = typeof tree !== 'undefined' ? tree : [];
    parent = typeof parent !== 'undefined' ? parent : { id: 0 };

    var children = array.filter(function(child) { return child.parentid == parent.id; });

    if (children.length > 0) {
        if (parent.id == 0) {
            tree = children;
        } else {
            parent['children'] = children
        }
        children.forEach(function(child) { unflatten(array, child) })
    }

    return tree;
}